#sec-3 {
    width: 100%;
    height: 100vh;

    .container {
        height: 100vh;
        margin-left:560px;
        display: flex;
        justify-content: center;
        align-items: center;

        .box {
            background: linear-gradient(180deg, var(--glassBg30) 0%, var(--glassBg0) 100%);
            width: 500px;
            height: 440px;
            padding: 2rem;
            display: flex;
            flex-direction: column;

            .headerBody {
                width: 100%;
                height: 20%;
                display: flex;
                flex-direction: column;
                align-items: center;

                .headerBox{
                    
                    margin-top: -60px;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                    align-items: center;

                    .CircleHeader {
                        background-color: var(--white);
                        width: 80px;
                        height: 80px;
                        border-radius: 100%;
                        position: relative;
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;

                        .navIcon {
                            font-size: 2.4rem;
                            color: var(--red1);
                            filter: drop-shadow(0px 2px 1px #00000066);
                        }
                    }

                    h2 {
                        margin-top: 10px;
                        color: var(--white);
                        text-shadow: 1px 1px 1px #00000080;
                    }
                }
            }

            .mainBody{
                box-sizing: border-box;
                width: 100%;
                height: 80%;
                padding-top: 20px;
                overflow-y: auto;
                scrollbar-width: thin;
                scrollbar-color: var(--glassBg50);
            }

            main::-webkit-scrollbar {
                width: 6px;
            }
            
            main::-webkit-scrollbar-track {
                background: transparent;
                margin-top: 20px;
            }
            
            main::-webkit-scrollbar-thumb {
                background-color: var(--glassBg30);
                border-radius: 20px;
                border-top: 2px solid rgba(255, 255, 255, 0.5);
                border-left: 2px solid rgba(255, 255, 255, 0.5);
                border-bottom: 2px solid rgba(0, 0, 0, 0.4);
                border-right: 2px solid rgba(0, 0, 0, 0.4);
            }
        }
    }
}

