

.itemContainer {
    background-color: var(--glassBg20);
    box-sizing: border-box;
    width: 96%;
    height: 80px;
    margin: 0% 1%;
    margin-right: 3%;
    padding: 10px;
    display: flex;
    margin-bottom: 30px;
    border-radius: 10px;
    border-bottom-left-radius: 50px;
    justify-content: space-between;
    box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066,-1px -1px 2px #ffffff;
    
    .circleItem {
        background-color: var(--white);
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        transform: translate(10px,-30px);
        z-index: 1;
        width: 80px;
        height: 80px;
        border-radius: 50%;
        box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;

        svg {
            position: relative;
            width: 80px;
            height: 80px;

            circle {
                width: 40px;
                height: 40px;
                fill: none;
                stroke-width: 10;
                stroke: var(--red);
                top: 50%;
                left: 50%;
                transform: translate(10px,10px);
                stroke-linecap: round;
                stroke-dasharray: 185;
            }

            circle:nth-child(1) {
                stroke: #D9D9D9;
                stroke-dasharray: 185;
            }
        }

        .number {
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            color: var(--title);
            font-size: 0.6rem;
            display: flex;
            justify-content: center;
            align-items: center;
        }

        .ImageIcon {
            width: 70%;
            height: 70%;
            margin: 15%;
            -webkit-touch-callout: none;
            -webkit-user-select: none;
            -khtml-user-select: none;
            -moz-user-select: none;
            -ms-user-select: none;
            user-select: none;
        }
    }

    .itemBox {
        width: 75%;
        display: flex;
        overflow-y: auto;
        scrollbar-width: thin;
        scrollbar-color: var(--glassBg50);

        .textContent {
            width: 100%;
            display: flex;
            flex-direction: column;

            .titleBox {
                height: 25px;
                width: 100%;
                margin: 0;
                margin-bottom: 5px;
                display: flex;
                justify-content: space-between;
                line-height: 25px;

                .titulo {
                    color: #EF8347;
                    font-size: 0.9rem;
                    font-weight: 700;
                    text-shadow: 1px 1px 1px #00000080;
                    margin-bottom: 0.8rem;
                }

                button {
                    background-color: var(--red);
                    width: 25px;
                    height: 25px;
                    margin-right: 2px;
                    color: var(--white);
                    font-size: 0.8rem;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    border: none;
                    border-radius: 50%;
                    cursor: pointer;
                    box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;
                }
            }

            p {
                color: var(--white);
                font-weight: 300;
                text-shadow: 0px 2px 2px #00000080;
                font-size: 0.8rem;
            }
        }
    }
    .itemBox::-webkit-scrollbar {
        width: 6px;
    }

    .itemBox::-webkit-scrollbar-track {
        background: transparent;
    }

    .itemBox::-webkit-scrollbar-thumb {
        background-color: var(--glassBg30);
        border-radius: 20px;
        border-top: 2px solid rgba(255, 255, 255, 0.5);
        border-left: 2px solid rgba(255, 255, 255, 0.5);
        border-bottom: 2px solid rgba(0, 0, 0, 0.4);
        border-right: 2px solid rgba(0, 0, 0, 0.4);
    }
}

.itemContainer:last-child {
    margin-bottom: 5px;
}

