  margin-right: 3%;
  padding: 10px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-bottom: 30px;
  border-radius: 10px;
  border-bottom-left-radius: 50px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066,-1px -1px 2px #ffffff;
          box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066,-1px -1px 2px #ffffff;
}

.itemContainer .circleItem {
  background-color: var(--white);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  position: relative;
  -webkit-transform: translate(10px, -30px);
          transform: translate(10px, -30px);
  z-index: 1;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  -webkit-box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;
          box-shadow: 1px 1px 2px #00000066, 2px 2px 5px #00000066;
}

.itemContainer .circleItem svg {
  position: relative;
  width: 80px;
  height: 80px;
}

.itemContainer .circleItem svg circle {
  width: 40px;
  height: 40px;
  fill: none;
  stroke-width: 10;
  stroke: var(--red);
  top: 50%;
  left: 50%;
  -webkit-transform: translate(10px, 10px);
          transform: translate(10px, 10px);
  stroke-linecap: round;
  stroke-dasharray: 185;
}

.itemContainer .circleItem svg circle:nth-child(1) {
  stroke: #D9D9D9;
  stroke-dasharray: 185;
}

.itemContainer .circleItem .number {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  color: var(--title);
  font-size: 0.6rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.itemContainer .circleItem .ImageIcon {
  width: 70%;
  height: 70%;
  margin: 15%;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.itemContainer .itemBox {
  width: 75%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  overflow-y: auto;
  scrollbar-width: thin;
  scrollbar-color: var(--glassBg50);
