}

.itemContainer .itemBox .textContent .titleBox button {
  background-color: var(--red);
  width: 25px;
  height: 25px;
  margin-right: 2px;
  color: var(--white);
  font-size: 0.8rem;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
