.itemContainer .itemBox .textContent p {
  color: var(--white);
  font-weight: 300;
  text-shadow: 0px 2px 2px #00000080;
  font-size: 0.8rem;
}

.itemContainer .itemBox::-webkit-scrollbar {
  width: 6px;
}

.itemContainer .itemBox::-webkit-scrollbar-track {
  background: transparent;
}

.itemContainer .itemBox::-webkit-scrollbar-thumb {
  background-color: var(--glassBg30);
  border-radius: 20px;
  border-top: 2px solid rgba(255, 255, 255, 0.5);
  border-left: 2px solid rgba(255, 255, 255, 0.5);
  border-bottom: 2px solid rgba(0, 0, 0, 0.4);
